<template>
  <div id="app">
    <img src=".././assets/blockcypher.png" width="100px" height="100px">
    <h1 class="mt-3">BlockCypher API</h1>
    <div class="container">

      <section>
        <template>
          <div class="main">
          <el-button type="primary" @click="handleBalance" style="display:inline-block; margin: 4%;">Get Address Balance</el-button>
          <div class="text"> Input transaction amount </div>
         <el-input-number v-model="amount" :min="1" style="display: inline-block;"></el-input-number>
         <el-button type="primary" @click="handlePayment">Make Transaction</el-button>
       </div>
       </template>
      </section>

       <section>
      <el-dialog title="Summary" :visible.sync="balanceVisible" :close-on-click-modal="false" >
      <div>
        Address: {{address}} <br>
        Unconfirmed Balance: {{unconfirmed_balance}}<br>
        Confirmed Balance: {{balance}}<br>
        Final Balance: {{ final_balance }}
      </div>
      
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="balanceVisible = false">Close</el-button>
      </div>
    </el-dialog>
       </section>

    </div>

  </div>
</template>

<script>
import {getBalance, makePayment} from './index.js'
import axios from 'axios'

export default {
  name: 'app',
  methods: {
  handleBalance: function() {
    this.loading = true // initializes loading
    getBalance(this.address).then(res => {
      console.log(res)
      // sets dynamically displayed text
      this.unconfirmed_balance = res.unconfirmed_balance
      this.balance = res.balance
      this.final_balance = res.final_balance
      this.loading = false
      this.balanceVisible = true; // displays pop up
    })
    
   },
   handlePayment: function(){
    this.loading = true
    makePayment(this.address, this.amount).then(res => {
      console.log(res)
      // generate notifications
      this.$notify({
          title: 'Success',
          message: 'Succesfully created transaction',
          type: 'success'
        });
      this.$notify.info({
          title: 'tx_ref',
          message: res.tx_ref,
          offset: 80
        });
      this.loading = false
    })
   }
 },
  data () {
    return {
      inputField: '',
      amount: 0,
      loading: false,
      todoList: [],
      balanceVisible: false,
      activeNames: ['1'],
      address: 'BwiPnFVCGTFZxqyqZ3fWAnKhoM7LKy5Zh6',
      unconfirmed_balance: '',
      balance: '',
      final_balance: ''

    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
main{
  text-align: center;

}
text{
  text-align: center;
}
h1, h2 {
  font-weight: normal;
}

h5 {
   margin-bottom: 0px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

</style>



